input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    min-height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

@keyframes logoLoad {
  0% {
    transform: scale(0.5); }
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

@keyframes buttonLoad {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

@keyframes openSite {
  0% {
    transform: translateX(-100%); }
  50% {
    transform: translateX(20%); }
  100% {
    transform: translateX(0); } }

@keyframes logoHeader {
  0% {
    transform: translateX(-100%); }
  50% {
    transform: translateX(100%); }
  100% {
    transform: translateX(0); } }

.header {
  display: block;
  max-width: 1024px;
  width: 100%;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .header {
      cursor: pointer; } }
  .header__logo {
    position: absolute;
    visibility: hidden;
    opacity: 0; }
    @media (max-width: 1023px) {
      .header__logo {
        cursor: default; } }

.open-site .header__wrapper {
